import React, { Component } from 'react';
import axios from 'axios';
import Loader from "./loader.gif"


export default class CreateUser extends Component {
  constructor(props) {
    super(props);

    this.onChangePunishee = this.onChangePunishee.bind(this);
    this.onSubmit = this.onSubmit.bind(this);

    this.state = {
      username: '',
      isLoading: false
    }
  }

  onChangePunishee(e) {
    this.setState({
      username: e.target.value
    })
  }

  onSubmit(e) {
    e.preventDefault();

    const user = {
      username: this.state.username
    }

    console.log(user);

    axios.post('https://punishee.herokuapp.com/punishees/add', user)
      .then(res => 
            this.setState({
              username: '',
              isLoading: true
            })
       )
      
       setTimeout(()=>{
         window.location = "/"
       },2000)
    
  }

  render() {
    return (
      
      <div>
        <h3>Create New Punishee</h3>
        <form onSubmit={this.onSubmit}>
          <div className="form-group"> 
            <label>Punishee Name: </label>
            <input  type="text"
                required
                className="form-control"
                value={this.state.username}
                onChange={this.onChangePunishee}
                />
          </div>
          <div className="form-group">
            <input type="submit" value="Create Punishee" className="btn btn-primary" />
          </div>
          {this.state.isLoading ? <div style={{"text-align": "center"}}><img src={Loader} alt="loader" /></div> : ""}
        </form>
      </div>
    )
  }
}
